* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
}

.page-container {
  padding: 40px 30px;
}

@media (min-width: 960px) {
  .page-container {
    padding: 108px 160px;
  }
}

todo-item {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
}
